.sheet {
  background-color: white;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
  display: block;
  margin: 0 auto;
  width: 1312px;
  position: relative;
  overflow: hidden;
}
footer {
  background-color: #333333;
  opacity: 1;
  height: 144px;
  float: left;
  width:100%;
  display: block;
}
.footer-text{
  text-align: center;
  font-family: Poppins;
  font-size: 12px;
  color:#9F9F9F;
  padding:42px;
}
.arrow-left,.arrow-right{
  color: #bbb;
  text-decoration: none;
  font-size: 45px;
  width: 32px;
  cursor:pointer;
}

.arrow-left:hover,.arrow-right:hover{
  color:rgb(48, 48, 48);
  transition: color 1s ease;
}
.arrow-left{
float:left;
top:260px;
position: relative;
}

.arrow-right{
float: right;
position: relative;
top:260px;
right: 160px
}

.dot:hover{
  background-color: rgb(48, 48, 48);
  transition: background-color 1s ease;
}

.dot{
  height:10px;
  width: 10px;
  margin:0 6px;
  background-color: #bbb;
  display: inline-block;
  transition: background-color 0.6s ease;
  border-radius: 50%;
  
}

.dot_active{
  height:10px;
  width: 10px;
  margin:0 6px;
  background-color: rgb(48, 48, 48);
  display: inline-block;
  transition: background-color 0.6s ease;
  border-radius: 50%;
}
.dotContainer{
position: relative;
left:269px
}

ul{
  padding-left:0px;
}
li{
list-style-type: none;
}

.school{
  font-size:15px;
  font-family: Poppins;
}


h1 {
  font-family: Poppins;
  font-size: 100px;
  letter-spacing: 4px;
  overflow: hidden;
  background: linear-gradient(90deg, #393939, #959595);
background-clip: text;
-webkit-text-fill-color: rgba(255, 255, 255, 0);
margin-left: 129px;
}

h2{
color:#333333;
 font-family: Poppins;
 font-size: 40px;
 margin-bottom: 0;
}

h3{
  color:#333333;
  font-family: Poppins;
  font-size: 24px;
  margin-top:0;
  border-bottom:1px solid #333333;
  padding-bottom: 6px;
  width:max-content;
  margin-bottom: 0;
}

h4{
  color:#333333;
  font-family: Poppins;
  font-size: 16px;
  margin-bottom: 17px;
}

p{
  color:#333333;
  font-family: Poppins;
  font-size:12px;
  margin:0 auto;
}

.fade{
-webkit-animation-name: fade;
-webkit-animation-duration: 1s;
animation-name: fade;
animation-duration: 1s
}

@-webkit-keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

.slider{
  clear: both;
  width: 496px;
  height: 250px;
  margin-top:23px;
  display: block;
  
}




.data-container{
  float:left;
}

.slider-container{
 
  overflow:hidden;
  display: inline-block;
  padding-left:32px;
}

.data-container-birthdate{
  margin-left:39px;
}
.data-container-adress,.data-container-birthdate{
  float:left;
}
.data-container-adress,.data-container-birthdate >p{
  
  color:#333333;
  font-family: Poppins;
  font-size: 12px;
}
.avatar-box{
  display: -webkit-flex;
  height: 496px;
  width: 386px;
  left:10%;
  float: left;
  margin-left:128px;
}
img{
  max-width:100%;
  height:auto;
}
.react-logo {
  animation: react-logo-spin infinite 20s linear;
  height: 7vmin;
}
@keyframes react-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
} 
