.logo{
  height: 70px;
  width: 70px;
  padding: 10px;
  box-sizing: border-box;
  transition-duration: .5s;
}

.logo:hover{
  padding: 0px;
}

.react-logo:hover{
  padding: 0px;
}

.react-logo-active{
  animation: react-logo-spin infinite 20s linear;
  height: 70px;
}

.logoActive{
  transition-duration: .5s;
  height: 70px;
  width: 70px;
  padding:0px;
}

.formContainer{
  height: 69%;
  width:100%;
  text-align: center;
  
}

.textCont{
  width: 98%;
  height: 80px;
  resize: none;
  float:left;
}

input, textarea{
border: solid 1px #bbb;
margin-bottom: 1%;
font-family: Poppins;
color:#333333;
font-size:12px;
}

.sendButton{
padding: 10px 30px;
background-color:#919191;
border-radius: 20px;
}
.sendButton:hover{
  background-color:#333333 ;
  color:#919191;
  transition: background-color 1s ease;
 
}

.email{
  float:left;
}

